import React, { useState, Fragment, useEffect } from "react";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import axios from "axios";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const List = () => {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [name, setName] = useState("");
  const [fatherName, setFatherName] = useState("");
  const [section, setSection] = useState("");
  const [mobile, setMobile] = useState("");
  const [address, setAddress] = useState("");

  const [editId, seteditId]= useState("");
  const [editName, seteditName] = useState("");
  const [editFatherName, seteditFatherName] = useState("");
  const [editSection, seteditSection] = useState("");
  const [editMobile, seteditMobile] = useState("");
  const [editAddress, seteditAddress] = useState("");

  const [data, SetData] = useState([]);

  useEffect(() => {
    getData();
  }, []);

  const getData = () => {
    axios
      .get("https://localhost:7272/api/Student")
      .then((result) => {
        SetData(result.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleEdit = (id) => {
    handleShow();
    axios.get(`https://localhost:7272/api/Student/${id}`)
    .then((result)=>{
      seteditName(result.data.name);
      seteditFatherName(result.data.fatherName);
      seteditSection(result.data.section);
      
      seteditAddress(result.data.address);
      seteditMobile(result.data.mobile)
      seteditId(id);
    }).
    catch((error)=>{
      console.log(error);
    })
  };
  const handleDelete = (id) => {
    //if (window.confirm("are you sure ?") === true) alert(id);
    axios
      .delete(`https://localhost:7272/api/Student/${id}`)
      .then((result) => {
        if (result.status == 200) {
          toast.success("Student data has been deleted");
          getData();
        }
      })
      .catch((error) => {
        toast.error(error);
      });
  };
  const handleUpdate = () => {
    axios
      .put(
        `https://localhost:7272/api/Student/${editId}`,
        {
          id: editId,
          name: editName,
          fatherName: editFatherName,
          section: editSection,
          mobile: editMobile,
          address: editAddress,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        getData();
        clear();
        toast.success("Student data has been Updated");
      })
      .catch((err) => {
        console.log("Server responded with error", err);
      });
  };

  const handleSave = () => {
    axios
      .post(
        "https://localhost:7272/api/Student",
        {
          name: name,
          fatherName: fatherName,
          section: section,
          mobile: mobile,
          address: address,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => {
        getData();
        clear();
        toast.success("Student data has been added");
      })
      .catch((err) => {
        console.log("Server responded with error", err);
      });
  };

  const clear = () => {
    setName("");
    setFatherName("");
    setSection("");
    setMobile("");
    setAddress("");

    seteditName("");
    seteditFatherName("");
    seteditSection("");
    seteditMobile("");
    seteditAddress("");
  };

  return (
    <Fragment>
      <ToastContainer />
      <Container>
        <Form onSubmit={() => handleSave()}>
          <Row>
            <Col>
              <Form.Group className="mb-1" controlId="formBasicPassword">
                <Form.Label className="font-weight-bold">Full Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Your Full Name" 
                  value={name} 
                  onChange={(e) => setName(e.target.value)}
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group className="mb-1" controlId="formBasicPassword">
                <Form.Label>Father's Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Your Father's Name"
                  value={fatherName}
                  onChange={(e) => setFatherName(e.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-1" controlId="formBasicPassword">
                <Form.Label>
                  Class Name you are willing to admit for
                </Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Class Name"
                  value={section}
                  onChange={(e) => setSection(e.target.value)}
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group className="mb-1" controlId="formBasicPassword">
                <Form.Label>Phone Number </Form.Label>
                <Form.Control
                  type="phone"
                  placeholder="Phone Number"
                  value={mobile}
                  onChange={(e) => setMobile(e.target.value)}
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group className="mb-1" controlId="formBasicPassword">
                <Form.Label>Address </Form.Label>
                <Form.Control
                  type="address"
                  placeholder="Address"
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>
          <Button variant="primary" type="submit">
            Submit
          </Button>
        </Form>
      </Container>

      <br />
      <br />
      <Table table-striped bordered hover table-sm table-responsive>
        <thead>
          <tr>
            <th>Student Name</th>
            <th>Father's Name</th>
            <th>Class Name</th>
            <th>Mobile Number</th>
            <th>Address</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {data && data.length > 0
            ? data.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{item.name}</td>
                    <td>{item.fatherName}</td>
                    <td>{item.section}</td>
                    <td>{item.mobile}</td>
                    <td>{item.address}</td>
                    <td colSpan={2}>
                      <button
                        className="btn btn-primary my-0"
                        onClick={() => handleEdit(item.id)}
                      >
                        Edit
                      </button>
                      &nbsp;
                      <button
                        className="btn btn-danger"
                        onClick={() => handleDelete(item.id)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                );
              })
            : "Loading..."}
        </tbody>
      </Table>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Update Student Data</Modal.Title>
        </Modal.Header>
        
        <Modal.Body>
          <Container>
          
            <Row>
              <Col>
                <Form.Group className="mb-1" controlId="formBasicPassword">
                  <Form.Label>Full Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Your Full Name"
                    value={editName}
                    onChange={(e) => seteditName(e.target.value)}
                  />
                </Form.Group>
              </Col>
              <Col>
                <Form.Group className="mb-1" controlId="formBasicPassword">
                  <Form.Label>Father's Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Your Father's Name"
                    value={editFatherName}
                    onChange={(e) => seteditFatherName(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col>
                <Form.Group className="mb-1" controlId="formBasicPassword">
                  <Form.Label>
                    Class Name you are willing to admit for{" "}
                  </Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Class Name"
                    value={editSection}
                    onChange={(e) => seteditSection(e.target.value)}
                  />
                </Form.Group>
              </Col>
              <Col>
                <Form.Group className="mb-1" controlId="formBasicPassword">
                  <Form.Label>Phone Number </Form.Label>
                  <Form.Control
                    type="phone"
                    placeholder="Phone Number"
                    value={editMobile}
                    onChange={(e) => seteditMobile(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col>
                <Form.Group className="mb-1" controlId="formBasicPassword">
                  <Form.Label>Address </Form.Label>
                  <Form.Control
                    type="address"
                    placeholder="Address"
                    value={editAddress}
                    onChange={(e) => seteditAddress(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            
          </Container>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleUpdate}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </Fragment>
  );
};
export default List;
