import React, { useState, Fragment, useEffect } from "react";
import "./search.css"
import Table from "react-bootstrap/Table";
import "./footerFixed.css";
import axios from "axios";
const Search = () => {
  const [data, SetData] = useState([]);
  const [input, setInput] = useState("");
  useEffect(() => {
    getData();
  }, []);

  const getData = (input) => {
    axios
      .get(`https://localhost:7272/api/Student/search/${input}`)
      .then((result) => {
        SetData(result.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  return (
    <Fragment>
      <div class="container">
        <div class="row height d-flex justify-content-center align-items-center">
          <div class="col-md-6">
            <div class="form">
              <input type="search" class="form-control form-input" placeholder="Enter Class name..." value={input} onChange={(e) => setInput(e.target.value)} />
              <button type="submit" className="btn btn-primary my-0 left-pan" onClick={() => getData(input)}> {" "}Search{" "} </button>
            </div>
          </div>
        </div>
      </div>
      <Table table-striped bordered hover table-sm>
        <thead>
          <tr>
            <th>Student Name</th>
            <th>Father's Name</th>
            <th>Mobile Number</th>
            <th>Address</th>
          </tr>
        </thead>
        <tbody>
          {data && data.length > 0
            ? data.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{item.name}</td>
                    <td>{item.fatherName}</td>
                    <td>{item.mobile}</td>
                    <td>{item.address}</td>
                  </tr>
                );
              })
            : "Loading data"}
        </tbody>
      </Table>
    </Fragment>
  ); 
}

export default Search;
